import React from 'react';
import {
  Button,
  Text,
  useColorMode,
  Icon,
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  IconButton,
  useToast,
} from '@chakra-ui/react';
import { SunIcon, MoonIcon } from '@chakra-ui/icons';
import { AiOutlineLogout } from 'react-icons/ai';
import { BiDotsHorizontal } from 'react-icons/bi';
import { IoMdLogOut } from 'react-icons/io';
import { FaUserAlt } from 'react-icons/fa';

export const DotMenuButton = () => {
  const { colorMode, toggleColorMode } = useColorMode();

  return (
    <Menu>
      <MenuButton as={Button} borderRadius={10}>
        <Icon as={BiDotsHorizontal} fontSize="25px" fontWeight="bold" />
      </MenuButton>
      <MenuList p={2} border="none" boxShadow="base">
        <MenuItem
          closeOnSelect={false}
          borderRadius={10}
          minH="50px"
          onClick={() => toggleColorMode()}
        >
          <IconButton
            icon={colorMode === 'light' ? <MoonIcon /> : <SunIcon />}
            bgColor={colorMode === 'light' ? 'gray.500' : 'gray.200'}
            color={colorMode === 'light' ? 'white' : 'black'}
          ></IconButton>
          <Text pl="2" fontWeight="bold">
            {colorMode === 'light' ? 'Dark Themes' : 'Light Theme'}
          </Text>
        </MenuItem>
      </MenuList>
    </Menu>
  );
};
